@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color:rgb(53, 53, 53);
}


.HomeScreen_home-screen__27yrC{
    height:100%;
    width:100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
}

.HomeScreen_home-screen__27yrC form{
    border:1px solid black;
}

.HomeScreen_home-screen__27yrC form input{
    border:none;
    outline:none;
    height:40px;
    padding:10px;
    font-size: 15px;
}

.HomeScreen_home-screen__27yrC form button {
    height:40px;
    width: 40px;
    background-color: white;
    border:none;   
    font-size: 15px;
}
.Warning_warning__30Quq{
    text-align: center;
    background-color: rgb(248, 107, 107);
}
.CityInfo_city-info__3lIRW{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
}

.CityInfo_city-info__3lIRW p:nth-child(4){
    text-align: center;
}

.CityInfo_city-info__3lIRW h1{
    border-bottom: 5px solid black;
    padding-bottom: 5px;
}

.CityInfo_city-info__3lIRW button{
    background-color: black;
    color:white;
    font-size: 15px;
    padding:10px;
    border:1px solid black;
    border-radius: 5px;
}
.Container_container__xoG1k{
    width:300px;
    height:350px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    overflow: hidden;
}

@media (max-width:400px){
    .Container_container__xoG1k{
        height:100vh;
        width: 100vw;
    }
}
